
import { reqCartList, reqDeleteCartById,reqUpdateCheckedById} from "@/api"
import { Promise } from "core-js";
const actions = {
    // 获取购物车列表数据
    async getCartList({commit}){
        let res = await reqCartList()
        if(res.code === 200){
            // 提交给mutations
            commit("GETCARTLIST",res.data)
            
        }
    },
    // 删除购物车某个产品
    async deleteCartListBySkuId({commit},skuId){
        let res = await reqDeleteCartById(skuId)
        if(res.code === 200){
            // 由于这个接口数据返回值为null，所以不需要提交给mutations
            return 'OK'  
        }else{
            return Promise.reject(new Error('faile'))
        }
    },
    // 修改购物车某个产品选中的状态
    async UpdateCheckedById({commit},{skuId,isChecked}){
        let res = await reqUpdateCheckedById(skuId,isChecked)
        if(res.code === 200){
            // 由于这个接口数据返回值为null，所以不需要提交给mutations
            return 'OK'  
        }else{
            return Promise.reject(new Error('faile'))
        }

    },
    // 删除全部勾选的产品
    deleteAllCheckedCart({dispatch,getters}){
        let PromiseAll = []
        // 获取购物车中全部的产品(是数组)
        getters.cartList.cartInfoList.forEach(item => {
            let Promise =  item.isChecked === 1 ? dispatch("deleteCartListBySkuId",item.skuId):""
            // 将每一次添加的Promise添加到数组当中
            PromiseAll.push(Promise)
        })
        return Promise.all(PromiseAll)
    },
    //修改全部商品的勾选的状态
    updateAllCartIsChecked({state, dispatch }, isChecked) {
            let PromiseAll = [];
            //获取购物车商品的个数,进行遍历
           state.cartList[0].cartInfoList.forEach(item => {
             //调用修改某一个商品的action【四次】
             let Promise = dispatch("UpdateCheckedById", { skuId: item.skuId, isChecked });
             PromiseAll.push(Promise);
           })
            //Promise.all():参数需要的是一个数组【数组里面需要promise】
            //Promise.all()执行一次,返回的是一个Promise对象,Promise对象状态：成功、失败取决于什么?
            //成功、还是失败取决于数组里面的promise状态:四个都成功、返回成功Promise、只要有一个失败、返回Promise失败状态！！！
            return Promise.all(PromiseAll);
    },
}

const mutations = {
    GETCARTLIST(state,cartList){
        state.cartList = cartList
    }
}

const state = {
    cartList:[]
}

const getters = {
    cartList(state){
        return state.cartList[0] || {}
    },
    // // 计算出的购物车的属性
    // cartInfoList(state){
    //     return state.cartList[0] || {}
    // }
}

//创建并暴露store
export default ({
    actions,
    mutations,
    state,
    getters
})