// 搜索模块
import { reqGetSearchInfo } from "@/api"
// search的小仓库
const actions = {
    // 获取search模块的数组
    async getSearchList({ commit }, params = {}) {
        // 当reqGetSearchInfo这个函数在调取服务器数据的时候，这里至少要传一个参数（空对象）
        // params形参：是当用户派发action的时候，第二个参数传递过来的，至少是一个空对象 
        let res = await reqGetSearchInfo(params)
        if (res.code == 200) {
            commit('GETSEARCHLIST', res.data)
        }
    }
}
const mutations = {
    GETSEARCHLIST(state, searchList) {
        state.searchList = searchList
    }
}
//初始化数据
const state = {
    searchList: {}
}
// 计算属性
// 项目当中getters主要作用是：简化仓库中的数据（简化数据）
// 可以把我们将来在组件当中需要用到的数据简化一下，这样将来组件在获取数据的时候就方便了
const getters = {
    // 假如网络状态不佳，state.searchList.goodsList返回的是一个undefined
    goodsList(state){
        return state.searchList.goodsList || []
    },
    trademarkList(state){
        return state.searchList.trademarkList || []
    },
    attrsList(state){ 
        return state.searchList.attrsList || []
    }
}

//创建并暴露store
export default ({
    actions,
    mutations,
    state,
    getters
})