// 临时身份游客模块
import {v4 as uuidv4} from 'uuid'
// 需要随机生成一个字符串，要求每次执行不能发生变化，游客身份还要持久存储
export const getUUID = () =>{
    // 1.从本地存储获取uuid(检查一下本地存储里面是否存在uuid)
    let uuid_token = localStorage.getItem('UUIDTOKEN')
    // let uuid_token = localStorage.getItem('UUIDTOKEN') || localStorage.setItem("uuid_token",uuidv4()) 
    // 2.如果本地存储没有uuid，那么我们生成uuid
    if(!uuid_token){
        // 生成uuid
        uuid_token = uuidv4()
        // 存储到本地
        localStorage.setItem('UUIDTOKEN',uuid_token)
    }
    return uuid_token
}