import { reqGoodsInfo,reqAddOrUpdateShopCart } from "@/api";
// 封装临时游客身份的模块uuid---生成一个随机字符串（仅此一次，不能再变化）
import {getUUID} from '@/utils/uuid_token'
const actions = {
    // 获取产品信息的action
    async getGoodInfo({commit},skuId){
        let res = await reqGoodsInfo(skuId)
        if(res.code == 200){
            commit("GETGOODINFO",res.data)
        }
    },
    // 将产品添加到购物车中  // 当前这个函数如果执行，返回的是一个Promise函数
    async addOrUpdateShopCart({commit},{skuId,skuNum}){
        // 加入购物车以后返回的解构
        // 加入购物车以后(发请求)，前台将参数带给服务器
        // 服务器写入数据成功，并没有返回其他的数据，只返回code=200，代表这次操作成功
        // 因为服务器没有返回其他数据，因此不需要三连环存储数据(dispatch,mutations,getters称为三连环)
        let res = await reqAddOrUpdateShopCart(skuId,skuNum)
        // 代表服务器加入购物车成功
        if(res.code == 200){
            return 'OK'
        }else{
            // 代表服务器加入购物车失败
            return Promise.reject(new Error('faile')) // index.js:36 Uncaught (in promise) Error: faile at Store.addOrUpdateShopCart 
            // new Promise((resolve, reject) => reject('报错了')) // index.js:34 Uncaught (in promise) 报错了
        }
    }
}
const mutations = {
    GETGOODINFO(state,goodInfo){
        state.goodInfo = goodInfo
    }
}
const state = {
    goodInfo:{},
    // 游客临时身份
    uuid_token:getUUID()
}
// 简化数据而生
const getters = {
    // 路径导航简化的数据
    categoryView(state){
        // 这里加空对象{}的原因：state.goodInfo初始状态是一个空对象{}，显然读取到的categoryView属性值为undefined。
        // categoryView属性值至少是一个空对象，所以加一个空对象报错就不会有了
        return state.goodInfo.categoryView || {}
    },
    // 简化产品信息的数据
    skuInfo(state){
        return state.goodInfo.skuInfo || {}
    },
    // 产品售卖属性的简化
    spuSaleAttrList(state){
        return state.goodInfo.spuSaleAttrList || []
    }
}

//创建并暴露store
export default ({
    actions,
    mutations,
    state,
    getters
})