// 配置路由的地方
import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
// 使用插件
Vue.use(VueRouter)
// 引入store
import store from '@/store'


// 解决连续点击搜索按钮多次出现错误
// 将VueRouter原型对象上的push先保存一份
let originPush = VueRouter.prototype.push
// 将VueRouter原型对象上的push先保存一份
let originReplace = VueRouter.prototype.replace
// 重写push|replace
// 第一个参数：告诉原来push方法，你往哪里跳转（传递哪些参数）
// call和apply区别
// 共同点：都可以调用函数一次，且可以篡改函数的上下文一次
// 不同点：call和apply传递参数：call传递参数用逗号隔开，apply方法执行，传递参数
VueRouter.prototype.push = function(location,resolve,reject){
    if(resolve&&reject){
        originPush.call(this,location,resolve,reject)
    }else{
        originPush.call(this,location,() =>{}, () => {})
    }
} 
VueRouter.prototype.replace = function(location,resolve,reject){
    if(resolve&&reject){
        originPush.call(this,location,resolve,reject)
    }else{
        originPush.call(this,location,() =>{}, () => {})
    }
} 

// 对外暴露VueRouter类的实例
let router =  new VueRouter({
    routes,
    // Vue-Router滚动行为
    scrollBehavior(to,from,savedPosition){
        // return 期望滚动到哪个的位置
        return {y:0} //y:0代表滚动条距离上方位0
    }
})

//全局守卫：前置守卫（在路由跳转之前判断） 
router.beforeEach(async(to,from,next)=>{
    // to:你要跳转到的地方
    // from:从哪个路由而来
    // next：放行函数   // mext() 全部放行  //next(path) 放行指定路由  //next(false) 不给跳转，回到原来的url地址

    // 用户登录有token,未登录则没有token
    let token = store.state.user.token
    // 用户信息
    let name = store.state.user.userInfo.name
    // 用户已经登陆的情况下
    if(token){
        // 用户已经登陆但想去登录页login[这里做校验，一票否决]
        if(to.path == "/login"){
            // 这里让它去首页
            next("/home")
        }
        else{
            // 用户登陆的情况下去其他页面，例如[home|search|detail|shopcart]
            // 再次做校验
            // 已有用户信息，放行
            if(name){
                next()
            }
            else{
                // 没有用户信息，派发action，让仓库存储用户信息再跳转
                try {
                    // 获取用户信息成功 放行
                    await store.dispatch("getUserInfo");
                    next()
                } catch (error) {
                    // 获取用户信息失败 token失效
                    // 清除token
                    await store.dispatch("userLogout")
                    next("/login")
                }
            }
            next()
        }
    }
    else{
        // 用户未登录的情况下 不能去交易(trade)、支付(pay|paysuccess)、购物车(shopcart)、个人中心(center)相关的不放行
        let toPath = to.path
        if(toPath.indexOf('/trade')!=-1 || toPath.indexOf('/pay')!=-1 || toPath.indexOf('/shopcart')!=-1 || toPath.indexOf('/center')!=-1 ){
            // 将未登录时想去但被拦截的信息，存储于url地址栏中[路由]
            next('/login?redirect='+toPath)
        }else{
            next()
        }
        // 登录的就放行
    }
})


export default router;