// 登录和注册的模块
import { reqGetCode,reqUserRegister,reqUserLogin,reqUserInfo,reqLogout} from '@/api'
import {setToken,getToken,removeToken} from '@/utils/token'
const actions = {
    // 获取验证码
    async getCode({commit},phone){
        // 正常情况，手机号发送给服务器，服务器会把短信发给用户，由于现在的后台没有这个能力，所以这里以结果的形式返回
        let res =  await reqGetCode(phone) //等待发送成功
        if(res.code == 200){
            // 注意：如果服务器把短信发给用户，这一步不用写
            commit("GETCODE",res.data)
            return 'OK'
        }else{
            return Promise.reject(new Error("faile"))
        }

    },
    // 用户注册
    async userRegister({commit},data){
        let res =  await reqUserRegister(data)
        if(res.code === 200){
            // 注意：如果服务器把短信发给用户，这一步不用写
            return 'OK'
        }else{
            return Promise.reject(new Error("faile"))
        }

    },
    // 用户登录业务[token]
    // data:用户名和密码
    async userLogin({commit},data){
        let res = await reqUserLogin(data)
        // 服务器下发token，用户唯一标识符
        // 通过带token找服务器要用户信息进行展示
        if(res.code === 200){
            // 用户登陆成功且获取到了token
            commit("USERLOGIN",res.data.token)
            // 持久化存储token
            // localStorage.setItem("TOKEN",res.data.token)
            setToken(res.data.token)
            return 'OK'
        }else{
            return Promise.reject(new Error("faile"))
        }
    },
    // 获取用户信息
    async getUserInfo({commit}){
        let res = await reqUserInfo();
        if(res.code === 200){
            // 提交用户信息
            commit("GETUSERINFO",res.data)
            return 'OK'
        }else{
            return Promise.reject(new Error("faile"))
        }
    },
    // 退出登录
    async userLogout({commit}){
        // 向服务器发起一次请求，通知服务器清除token
        let res = await reqLogout()
        // action里不能操作state，提交mutation修改state
        if(res.code === 200){
            commit("CLEAR")
            return 'OK'
        }else{
            Promise.reject(new Error("faile"))
        }
    },
}
const mutations = {
    GETCODE(state,code){
        state.code = code
    },
    USERLOGIN(state,token){
        state.token = token
    },
    GETUSERINFO(state,userInfo){
        state.userInfo = userInfo
    },
    // 清除本地数据
    CLEAR(state){
        // 将仓库中的用户信息清空
        state.code = ''
        state.userInfo = {}
        // 本地存储数据清空
        removeToken()
    }
}

const state = {
    code:'',
    token: getToken(),
    userInfo:{},
}

const getters = {
}

//创建并暴露store
export default ({
    actions,
    mutations,
    state,
    getters
})