// 配置路由
// 引入一级路由组件  使用了路由懒加载
// 引入二级路由组件
import MyOrder from '@/pages/Center/myOrder'
import GroupOrder from '@/pages/Center/groupOrder'
import ActivitiesConcerned from '@/pages/Center/ActivitiesConcerned'
import AlbumConcerned from '@/pages/Center/AlbumConcerned'
import BrandsConcerned from '@/pages/Center/BrandsConcerned'
import BrowseHistory from '@/pages/Center/BrowseHistory'
import CancelOrderRecord from '@/pages/Center/CancelOrderRecord'
import EnergySavingSubsidy from '@/pages/Center/EnergySavingSubsidy'
import EvaluationSheet from '@/pages/Center/EvaluationSheet'
import FlowGasStation from '@/pages/Center/FlowGasStation'
import GoldTrusteeship from '@/pages/Center/GoldTrusteeship'
import LocalLifeOrder from '@/pages/Center/LocalLifeOrder'
import MedicalServices from '@/pages/Center/MedicalServices'
import MyBusinessHall from '@/pages/Center/MyBusinessHall'
import MyPresales from '@/pages/Center/MyPresales'
import MyRecycleSlip from '@/pages/Center/MyRecycleSlip'
import PriceProtection from '@/pages/Center/PriceProtection'
import ProductsConcerned from '@/pages/Center/ProductsConcerned'
import PurchaseConsultation from '@/pages/Center/PurchaseConsultation'
import RegularDelivery from '@/pages/Center/RegularDelivery'
import ReturnAndReplacement from '@/pages/Center/ReturnAndReplacement'
import ShopsConcerned from '@/pages/Center/ShopsConcerned'
import Suggestions from '@/pages/Center/Suggestions'
import XiaoshiCommunication from '@/pages/Center/XiaoshiCommunication'
import XiaoshiOrderOnBehalf from '@/pages/Center/XiaoshiOrderOnBehalf'
import TransactionDispute from '@/pages/Center/TransactionDispute'
import MyInvoice from '@/pages/Center/MyInvoice'
import PersonalInformation from '@/pages/Center/PersonalInformation'
import ShipToAddress from '@/pages/Center/ShipToAddress'
// 第一：路径的前面需要有/   第二：路径中的单词全部小写  
// 第三：component右侧不能加引号[字符串，组件是对象（VueComponent类的实例）] 
export default [
    {
        path: '/home',
        name: 'home',
        component: ()=>import("@/pages/Home"),
        // 路由元信息只能叫meta
        meta: { requireAuth: true }
    },
    {
        path: '/login',
        name: 'login',
        component: ()=>import("@/pages/Login"),
        meta: { requireAuth: false }
    },
    {
        path: '/register',
        name: 'register',
        component: ()=>import("@/pages/Register"),
        meta: { requireAuth: false }
    },
    {
        // 在注册路由的时候,如果这里占位，切记务必要传递params
        // 下面这种写法代表params参数可传可不传 ?(正则：两次代表出现次数0|1)  ?必须带上
        path: '/search/:keyword?',
        name: 'search',
        component: ()=>import("@/pages/Search"),
        meta: { requireAuth: true },
        // 路由可以给组件传递props的
        props:(route) => ({
            keyword:route.params.keyword,
            big:route.query.big
        })
    },
    {
        path: '/detail/:skuid',
        name: 'detail',
        component: ()=>import("@/pages/Detail"),
        meta: { requireAuth: true }
    },
    {
        path: '/addcartsuccess',
        name: 'addcartsuccess',
        component: ()=>import("@/pages/AddCartSuccess"),
        meta: { requireAuth: true }
    },
    {
        path: '/shopcart',
        name: 'shopcart',
        component: ()=>import("@/pages/ShopCart"),
        meta: { requireAuth: true }
    },
    {
        path: '/trade',
        name: 'trade',
        component: ()=>import("@/pages/Trade"),
        meta: { requireAuth: true },
        // 路由独享守卫
        beforeEnter: (to, from, next) => {
            // 去交易页面，必须是从购物车而来
            if(from.path == '/shopcart'){
                next()
            }else{
                // 从其他页面来的 中断当前导航
                next(false)
            }
        }
    },
    {
        path: '/pay',
        name: 'pay',
        component: ()=>import("@/pages/Pay"),
        meta: { requireAuth: true },
        beforeEnter: (to, from, next) => {
            if(from.path == '/trade'){
                next()
            }else{
                next(false)
            }
        }
    },
    {
        path: '/paysuccess',
        name: 'paysuccess',
        component: ()=>import("@/pages/PaySuccess"),
        meta: { requireAuth: true }
    },
    {
        path: '/center',
        name: 'center',
        component: ()=>import("@/pages/Center"),
        meta: { requireAuth: true },
        children:[
            {
                path: 'myorder',
                name: 'myorder',
                component: ()=>import("@/pages/Center/myOrder"),
                meta: { requireAuth: true },
            },
            {
                path: 'grouporder',
                name: 'grouporder',
                component: ()=>import("@/pages/Center/groupOrder"),
                meta: { requireAuth: true },
            },
            {
                path: 'activitiesconcerned',
                name: 'activitiesconcerned',
                component: ()=>import("@/pages/Center/ActivitiesConcerned"),
                meta: { requireAuth: true },
            },
            {
                path: 'albumconcerned',
                name: 'albumconcerned',
                component: ()=>import("@/pages/Center/AlbumConcerned"),
                meta: { requireAuth: true },
            },
            {
                path: 'brandsconcerned',
                name: 'brandsconcerned',
                component: ()=>import("@/pages/Center/BrandsConcerned"),
                meta: { requireAuth: true },
            },
            {
                path: 'browsehistory',
                name: 'browsehistory',
                component: ()=>import("@/pages/Center/BrowseHistory"),
                meta: { requireAuth: true },
            },
            {
                path: 'cancelorderrecord',
                name: 'cancelorderrecord',
                component: ()=>import("@/pages/Center/CancelOrderRecord"),
                meta: { requireAuth: true },
            },
            {
                path: 'energysavingsubsidy',
                name: 'energysavingsubsidy',
                component: ()=>import("@/pages/Center/EnergySavingSubsidy"),
                meta: { requireAuth: true },
            },
            {
                path: 'evaluationsheet',
                name: 'evaluationsheet',
                component: ()=>import("@/pages/Center/EvaluationSheet"),
                meta: { requireAuth: true },
            },
            {
                path: 'flowgasstation',
                name: 'flowgasstation',
                component:  ()=>import("@/pages/Center/FlowGasStation"),
                meta: { requireAuth: true },
            },
            {
                path: 'goldtrusteeship',
                name: 'goldtrusteeship',
                component: ()=>import("@/pages/Center/GoldTrusteeship"),
                meta: { requireAuth: true },
            },
            {
                path: 'locallifeorder',
                name: 'locallifeorder',
                component: ()=>import("@/pages/Center/LocalLifeOrder"),
                meta: { requireAuth: true },
            },
            {
                path: 'medicalservices',
                name: 'medicalservices',
                component: ()=>import("@/pages/Center/MedicalServices"),
                meta: { requireAuth: true },
            },
            {
                path: 'mybusinesshall',
                name: 'mybusinesshall',
                component: ()=>import("@/pages/Center/MyBusinessHall"),
                meta: { requireAuth: true },
            },
            {
                path: 'mypresales',
                name: 'mypresales',
                component: ()=>import("@/pages/Center/MyPresales"),
                meta: { requireAuth: true },
            },
            {
                path: 'myrecycleslip',
                name: 'myrecycleslip',
                component:()=>import("@/pages/Center/MyRecycleSlip"),
                meta: { requireAuth: true },
            },
            {
                path: 'priceprotection',
                name: 'priceprotection',
                component:()=>import("@/pages/Center/PriceProtection"),
                meta: { requireAuth: true },
            },
            {
                path: 'productsconcerned',
                name: 'productsconcerned',
                component: ()=>import("@/pages/Center/ProductsConcerned"),
                meta: { requireAuth: true },
            },
            {
                path: 'purchaseconsultation',
                name: 'purchaseconsultation',
                component: ()=>import("@/pages/Center/PurchaseConsultation"),
                meta: { requireAuth: true },
            },
            {
                path: 'regulardelivery',
                name: 'regulardelivery',
                component: ()=>import("@/pages/Center/RegularDelivery"),
                meta: { requireAuth: true },
            },
            {
                path: 'returnandreplacement',
                name: 'returnandreplacement',
                component: ()=>import("@/pages/Center/ReturnAndReplacement"),
                meta: { requireAuth: true },
            },
            {
                path: 'shopsconcerned',
                name: 'shopsconcerned',
                component: ()=>import("@/pages/Center/ShopsConcerned"),
                meta: { requireAuth: true },
            },
            {
                path: 'suggestions',
                name: 'suggestions',
                component: ()=>import("@/pages/Center/Suggestions"),
                meta: { requireAuth: true },
            },
            {
                path: 'xiaoshicommunication',
                name: 'xiaoshicommunication',
                component: ()=>import("@/pages/Center/XiaoshiCommunication"),
                meta: { requireAuth: true },
            },
            {
                path: 'transactiondispute',
                name: 'transactiondispute',
                component: ()=>import("@/pages/Center/TransactionDispute"),
                meta: { requireAuth: true },
            },
            {
                path: 'myinvoice',
                name: 'myinvoice',
                component: ()=>import("@/pages/Center/MyInvoice"),
                meta: { requireAuth: true },
            },
            {
                path: 'xiaoshiorderonbehalf',
                name: 'xiaoshiorderonbehalf',
                component: ()=>import("@/pages/Center/XiaoshiOrderOnBehalf"),
                meta: { requireAuth: true },
            }, 
            {
                path: 'personalinformation',
                name: 'personalinformation',
                component: ()=>import("@/pages/Center/PersonalInformation"),
                meta: { requireAuth: true },
            }, 
            {
                path: 'shiptoaddress',
                name: 'shiptoaddress',
                component: ()=>import("@/pages/Center/ShipToAddress"),
                meta: { requireAuth: true },
            }, 

            {
                path: '/center',
                redirect: '/center/myorder'
            }
        ]
    },
    // 重定向：在项目跑起来的时候，访问/立马让他定向到首页
    {
        path: '/',
        redirect: '/home'
    },
]